import Vue from 'vue'
import Router from 'vue-router'
import omikuji from '@/components/omikuji'
import mochi from '@/components/mochi' //①import 〇〇〇.vueを @/components/〇〇で指定
import osechi from '@/components/osechi' 
import last from '@/components/last' 




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'omikuji',
      component: omikuji
    },


//②鏡餅
   {
      path: '/mochi',
      name: 'mochi',
      component: mochi
    },

//③おせち
   {
      path: '/osechi',
      name: 'osechi',
      component: osechi
    },

//③さいごに
   {
    path: '/last',
    name: 'last',
    component: last
  },


  ]
})



