<template>

  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>明けましておめでとう</h2>
    <img class="img" src="../assets/osechi.png" width="200">
    
   
    <ul>
      <li>
        <a
          href="https://search.rakuten.co.jp/search/mall/%E3%81%8A%E3%81%9B%E3%81%A1/"
          target="_blank"
        >
          おせっち
          
        </a>
      </li>

    </ul>
  </div>
  
</template>


<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'おせち'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



