<template>
  <div class="output">

  <!-- 何個のコンテンツを並べるかin 数  -->
    <div class="content" v-for="i in 1" :key=i>
    
    <h3> {{ name }} </h3>
    <p> {{ text }} </p>
  </div>
  </div>
</template>

<script>
export default {
  name: 'Output',
  data () {
    return {
      name: 'さいごに',
      text: '今回はvue.jsのrouterを使って作ってみました。初めて使いましたがVue.jsのファイルの使い方が少し分かるようになって嬉しかったです。'
      //.repeat(3)サンプルの(３回繰り返しのコード)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div.output {
  text-align: center;
}

div.content {
  background-color: #eee;
  display: inline-block;
  margin: 10px;
  width: 30%;
}

h3 {
  color: white;
}


p {
  width: 90%;
  display: inline-block;
  text-align: left;
}


</style>


